
@charset "UTF-8";
body, div, span, header, footer, nav, section, aside, article,
ul, dl, dt, dd, li, a, p, h1, h2, h3, h4, h5, h6, i, b, textarea,
button, input, select, figure {
  padding: 0;
  margin: 0;
  list-style: none;
  font-style: normal;
  text-decoration: none;
  border: none;
  color: #333;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}
body:hover, div:hover, span:hover, header:hover, footer:hover, nav:hover, section:hover, aside:hover, article:hover,
  ul:hover, dl:hover, dt:hover, dd:hover, li:hover, a:hover, p:hover, h1:hover, h2:hover, h3:hover, h4:hover, h5:hover, h6:hover, i:hover, b:hover, textarea:hover,
  button:hover, input:hover, select:hover, figure:hover {
    outline: none;
}
html, body {
  height: 100%;
  width: 100%;
  font-size: 15px;
  background-color: #fafafa;
}



